import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import zhiyuanzheList from '@/views/pages/zhiyuanzhe/list'
import zhiyuanzheDetail from '@/views/pages/zhiyuanzhe/formModel'
import zhiyuanzheAdd from '@/views/pages/zhiyuanzhe/formAdd'
import zhiyuanzheRegister from '@/views/pages/zhiyuanzhe/register'
import zhiyuanzheCenter from '@/views/pages/zhiyuanzhe/center'
import zhiyuanzhepeixunList from '@/views/pages/zhiyuanzhepeixun/list'
import zhiyuanzhepeixunDetail from '@/views/pages/zhiyuanzhepeixun/formModel'
import zhiyuanzhepeixunAdd from '@/views/pages/zhiyuanzhepeixun/formAdd'
import fengcaizhanshiList from '@/views/pages/fengcaizhanshi/list'
import fengcaizhanshiDetail from '@/views/pages/fengcaizhanshi/formModel'
import fengcaizhanshiAdd from '@/views/pages/fengcaizhanshi/formAdd'
import zhiyuanfuwugangweiList from '@/views/pages/zhiyuanfuwugangwei/list'
import zhiyuanfuwugangweiDetail from '@/views/pages/zhiyuanfuwugangwei/formModel'
import zhiyuanfuwugangweiAdd from '@/views/pages/zhiyuanfuwugangwei/formAdd'
import zhiyuanfuwuqiandaoList from '@/views/pages/zhiyuanfuwuqiandao/list'
import zhiyuanfuwuqiandaoDetail from '@/views/pages/zhiyuanfuwuqiandao/formModel'
import zhiyuanfuwuqiandaoAdd from '@/views/pages/zhiyuanfuwuqiandao/formAdd'
import renwufenpeiList from '@/views/pages/renwufenpei/list'
import renwufenpeiDetail from '@/views/pages/renwufenpei/formModel'
import renwufenpeiAdd from '@/views/pages/renwufenpei/formAdd'
import renwuwanchengList from '@/views/pages/renwuwancheng/list'
import renwuwanchengDetail from '@/views/pages/renwuwancheng/formModel'
import renwuwanchengAdd from '@/views/pages/renwuwancheng/formAdd'
import zhiyuanfuwuqiantuiList from '@/views/pages/zhiyuanfuwuqiantui/list'
import zhiyuanfuwuqiantuiDetail from '@/views/pages/zhiyuanfuwuqiantui/formModel'
import zhiyuanfuwuqiantuiAdd from '@/views/pages/zhiyuanfuwuqiantui/formAdd'
import kaoqinxinxiList from '@/views/pages/kaoqinxinxi/list'
import kaoqinxinxiDetail from '@/views/pages/kaoqinxinxi/formModel'
import kaoqinxinxiAdd from '@/views/pages/kaoqinxinxi/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'zhiyuanzheList',
			component: zhiyuanzheList
		}, {
			path: 'zhiyuanzheDetail',
			component: zhiyuanzheDetail
		}, {
			path: 'zhiyuanzheAdd',
			component: zhiyuanzheAdd
		}
		, {
			path: 'zhiyuanzheCenter',
			component: zhiyuanzheCenter
		}
		, {
			path: 'zhiyuanzhepeixunList',
			component: zhiyuanzhepeixunList
		}, {
			path: 'zhiyuanzhepeixunDetail',
			component: zhiyuanzhepeixunDetail
		}, {
			path: 'zhiyuanzhepeixunAdd',
			component: zhiyuanzhepeixunAdd
		}
		, {
			path: 'fengcaizhanshiList',
			component: fengcaizhanshiList
		}, {
			path: 'fengcaizhanshiDetail',
			component: fengcaizhanshiDetail
		}, {
			path: 'fengcaizhanshiAdd',
			component: fengcaizhanshiAdd
		}
		, {
			path: 'zhiyuanfuwugangweiList',
			component: zhiyuanfuwugangweiList
		}, {
			path: 'zhiyuanfuwugangweiDetail',
			component: zhiyuanfuwugangweiDetail
		}, {
			path: 'zhiyuanfuwugangweiAdd',
			component: zhiyuanfuwugangweiAdd
		}
		, {
			path: 'zhiyuanfuwuqiandaoList',
			component: zhiyuanfuwuqiandaoList
		}, {
			path: 'zhiyuanfuwuqiandaoDetail',
			component: zhiyuanfuwuqiandaoDetail
		}, {
			path: 'zhiyuanfuwuqiandaoAdd',
			component: zhiyuanfuwuqiandaoAdd
		}
		, {
			path: 'renwufenpeiList',
			component: renwufenpeiList
		}, {
			path: 'renwufenpeiDetail',
			component: renwufenpeiDetail
		}, {
			path: 'renwufenpeiAdd',
			component: renwufenpeiAdd
		}
		, {
			path: 'renwuwanchengList',
			component: renwuwanchengList
		}, {
			path: 'renwuwanchengDetail',
			component: renwuwanchengDetail
		}, {
			path: 'renwuwanchengAdd',
			component: renwuwanchengAdd
		}
		, {
			path: 'zhiyuanfuwuqiantuiList',
			component: zhiyuanfuwuqiantuiList
		}, {
			path: 'zhiyuanfuwuqiantuiDetail',
			component: zhiyuanfuwuqiantuiDetail
		}, {
			path: 'zhiyuanfuwuqiantuiAdd',
			component: zhiyuanfuwuqiantuiAdd
		}
		, {
			path: 'kaoqinxinxiList',
			component: kaoqinxinxiList
		}, {
			path: 'kaoqinxinxiDetail',
			component: kaoqinxinxiDetail
		}, {
			path: 'kaoqinxinxiAdd',
			component: kaoqinxinxiAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/zhiyuanzheRegister',
		component: zhiyuanzheRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
